export default function createList(data, settings, isSubList = false) {
  const suffix = isSubList ? '__sub-' : '__';
  const baseClass = settings.className + suffix;

  const itemsMarkup = `
    ${data
      .map(item => {
        if (item.id.startsWith(settings.className))
          return `<li class="${baseClass}item" data-sn-section="${item.id}">
              <a class="${baseClass}link" href="#${item.id}">${item.text}</a>
              ${
                item.subSections && item.subSections.length
                  ? `${createList(item.subSections, settings, true)}`
                  : ''
              }
            </li>`;
      })
      .join('')}
  `;

  const list = `
    <ol class="${baseClass}list">
      ${itemsMarkup}
    </ol>
  `;

  return list;
}
